@import "../base/settings";

.overlay {
    background-color: white;
    position: fixed;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    opacity: 1;
    top: 0px;
    right: 0px;
    z-index: 1000;
}

.logReg {
    padding: 30px;
    position: fixed;
    left: 50%;
    top: 10%;
    transform: translate(-50%, 0%);
    height: auto;
    width: 20rem;
    background: $pri_color 0% 0% no-repeat padding-box;
    border: 1px solid #707070;
    border-radius: 58px;
    opacity: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 4px -1px 50px rgb(95, 95, 95);
    z-index: 2000;

    div {
        width: 100%;
    }

    input {
        margin-top: 10px;
        margin-bottom: 10px;
    }
}

.logNav {
    height: 30px;
    border: 1px solid white;
    width: 80%;
    border-radius: 27px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    box-shadow: 0px 0px 10px rgb(65, 65, 65);
    margin-bottom: 10px;
    span {
        padding: 2px 0px;
        text-align: center;
        color: $white_text;
        font: Regular Helvetica Neue;
        font-size: 1.3rem;
        cursor: pointer;
    }
    span:hover {
        background-color: $l_pri_color;
    }

    .active {
        background-color: $d_pri_color;
    }
}

.log {
    border-right: 1px solid white;
    width: 50%;
    border-bottom-left-radius: 27px;
    border-top-left-radius: 27px;
}
.reg {
    width: 50%;
    border-bottom-right-radius: 27px;
    border-top-right-radius: 27px;
}

input {
    background: #ffffff 0% 0% no-repeat padding-box;
    border: 1px solid #707070;
    border-radius: 27px;
    opacity: 1;
    width: 80%;
    // height: 30px;
    font-size: 1rem;
    padding: 10px;
}
input:focus {
    outline: none;
    border: 1px solid greenyellow;
}

button {
    padding: 5px;
    width: 30%;
    font-size: 1rem;
    background-color: $pri_color;
    border-radius: 27px;
    cursor: pointer;
}
button:active {
    background-color: rgb(199, 199, 199);
}
button:focus {
    outline: none;
}

.btnbtn {
    margin-top: 10px;
}

.gotohomebtn {
    position: fixed;
    top: 50px;
    left: 50px;
    border-radius: 100px;
    border: solid black 2px;
    z-index: 3000;
    cursor: pointer;
}

.loadingsvg {
    height: 20px;
    animation-name: rotate;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    margin: 0px;
}

@keyframes rotate {
    from {
        transform: rotateZ(0deg);
    }
    to {
        transform: rotateZ(-360deg);
    }
}
