@import "../base/settings";

.quotes_con {
    background-image: url("http://demo.agnidesigns.com/candy/wp-content/themes/candy/img/about_bg.jpg");
    height: 400px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100%;
    background-color: #464646;
    background-attachment: fixed;
    box-sizing: border-box;
    padding: 150px 30px;
}

.quote {
    width: 70%;
    color: white;
    text-align: center;
    margin: auto;

    p {
        font-size: large;
    }

    h2 {
        font-size: medium;
    }

    img {
        border-radius: 100px;
    }
}
