@import "../base/settings";

.cont_det_con {
    background-color: $grey_back;
    display: flex;
    flex-direction: row;
    padding: 140px 30px;
    justify-content: space-evenly;
    flex-wrap: wrap;
}

.detItems {
    display: flex;
    padding: 20px 10px;
}
.det_img {
    border-right: 1px solid grey;
    display: inline;
    padding: 20px 40px;
    img {
        height: 40px;
        margin: auto;
    }
}

.det_body {
    display: inline;
    margin-left: 20px;

    h1 {
        margin: 5px;
        font-size: 27px;
    }
    p {
        margin: 0px;
        color: $grey_text;
    }
}
